import React from 'react'
import "./test.scss"


function Test({ month, period, period2, period3, rates, rates2, rates3 }) {
    return (
        <div className="rates-wrapper-test">
            <h3>{month}</h3>
            <div className="titles-test">
                <h4>Period</h4>
                <h4>Weekly Rates</h4>                
            </div> 
            <div className="rates-container-test">
                <div className="rates-test">
                    <p>{period}</p>
                    <p>{rates}</p>
                </div>
                { period2 ? <div className="rates-test">
                    <p>{period2}</p>
                    <p>{rates2}</p>
                    </div> : <></> } 
                { period3 ? <div className="rates-test">
                    <p>{period3}</p>
                    <p>{rates3}</p>
                    </div> : <></> } 
            </div>   
        </div>
    )
}

export default Test